import React from 'react';
import ReactDOM from 'react-dom';

export default class DeleteDataModal extends React.Component
{
	constructor(props){
		super(props);
	}	
	
	render(){
		return (
			<div className="modal fade" id="deleteData">
			  <div className="modal-dialog">
			    <div className="modal-content">
			 
			      <div className="modal-header">
			        <h4 className="modal-title">确认删除？</h4>
			        <button type="button" className="close" data-dismiss="modal">&times;</button>
			      </div>

			      <div className="modal-body">
			        <div id="deleteDataError" className="text-danger error"></div>
			        你正在删除[<span id="deleteClientId"></span>]
			      </div>
			 
			      <div className="modal-footer">
			        <button id="deleteDataDelete" data-resourceId="" type="button" className="btn btn-danger" onclick="deleteResourceData()">删除</button>
			        <button id="deleteDataClose" type="button" className="btn btn-secondary" data-dismiss="modal">关闭</button>
			      </div>
			 
			    </div>
			  </div>
			</div>
		);
	}
}